___CSS_LOADER_EXPORT___.push([module.id, "\n.version-text[data-v-77bc5a41] {\n    font-size: 16px;\n    color: #616161;\n}\n.add-new-version[data-v-77bc5a41] {\n    border-radius: 4px;\n    border: 1px solid #151233;\n    color: #151233;\n    font-weight: 500;\n    font-size: 14px;\n    background-color: transparent;\n    position: absolute;\n    right: 40px;\n    width: 94px;\n    height: 32px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n.delete-version[data-v-77bc5a41] {\n    font-size: 16px;\n    color: #757575;\n    cursor: pointer;\n}\n.title[data-v-77bc5a41] {\n    font-style: normal;\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    white-space: nowrap;\n    color: #151233;\n    margin-top: 40px;\n    margin-bottom: 8px;\n}", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/appVersions.vue"],"names":[],"mappings":";AA2HA;IACI,eAAe;IACf,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;IACX,WAAW;IACX,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;AAEA;IACI,eAAe;IACf,cAAc;IACd,eAAe;AAEnB;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;IACd,gBAAgB;IAChB,kBAAkB;AACtB","sourcesContent":["<script setup lang=\"ts\">\nimport { defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { IAppVersion, IApplication } from \"../../../../domain/model/models\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IApplicationVersionUseCase } from \"../../../../domain/useCase/IApplicationVersionUseCase\";\nimport { ITableMethods } from \"../../../../domain/useCase/ITableMethods\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport CreateAppVer from \"./createAppVersion.vue\";\n// import { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n\ninterface IAppVersionProp {\n    app: IApplication | undefined\n}\nenum ShowState {\n    ListAppVer = 1,\n    CreateaAppVer = 2,\n}\nconst props = defineProps<IAppVersionProp>();\n// const viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst tableMethods = container.resolve<ITableMethods>(ITableMethods);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst applicationVersionUseCase = container.resolve<IApplicationVersionUseCase>(IApplicationVersionUseCase);\nconst emit = defineEmits([\"hideLeftNav\", \"showLeftNav\", \"showHeader\", \"hideHeader\"]);\nconst appVersions = ref<Array<IAppVersion>>([]);\nconst appVerDisplayState = ref(ShowState.ListAppVer);\nconst title = ref(\"App Versions\");\nconst pageDesc = ref(\"You can see versions of your application.\");\n\n\nconst tableColumns = ref([\"version\", \"ID\", \"description\", \"createdBy\", \"createDate\", \"actions\"]);\nconst tableOptions = ref({\n    filterable: true,\n    perPage: 3,\n    perPageValues: [],\n    headings: {\n        version: localization.get(\"version\"),\n        ID: localization.get(\"ID\"),\n        description: localization.get(\"description\"),\n        createdBy: localization.get(\"createdBy\"),\n        createDate: localization.get(\"createDate\"),\n        actions: \"\"\n    },\n    resizableColumns: false,\n    skin: 'app-versions',\n    texts: {\n        count: \"\",\n        noResults: \"No matching records\",\n    },\n})\n\n\nfunction listAppVersions() {\n    const appID = props.app!.ID;\n    executor.execute(async () => {\n        appVersions.value = await applicationVersionUseCase.listApplicationVersions(appID);\n    }, { loading: true })\n\n}\nlistAppVersions();\n\nfunction deleteVersion(appVersionId: string) {\n    executor.execute(async () => {\n        await applicationVersionUseCase.deleteApplicationVersion(appVersionId);\n        listAppVersions();\n    }, { loading: true })\n}\nfunction createVersionArtifact(appVersionId: string) {\n    executor.execute(async () => {\n        await applicationVersionUseCase.createVersionArtifact(appVersionId);\n    }, { loading: true })\n}\n// function addNewVersionDialog() {\n//     const closeCb = (reason: \"close\" | \"outclick\" | \"component\") => {\n//         if(reason == \"component\") {\n//             listAppVersions();\n//         }\n//     }\n//     dialog.showDialog(defineAsyncComponent(() => import(`./createAppVersion.vue`)),\n//                       { closable: true, title: \"Create App Version\", width: \"800px\", closeCb }, {app: props.app})\n// }\nfunction changeDisplay(state: number) {\n    appVerDisplayState.value += state\n    switch (appVerDisplayState.value) {\n        case ShowState.ListAppVer: title.value = \"App Versions\"; emit(\"showLeftNav\");\n            pageDesc.value = \"You can see versions of your application.\"; break;\n        case ShowState.CreateaAppVer: title.value = \"New App Version\"; emit(\"hideLeftNav\");\n            pageDesc.value = \"You can create new versions of your application\"; break;\n        default: break;\n    }\n    emit(\"hideHeader\")\n}\n</script>\n<template>\n    <div>\n        <div class=\"title\">{{ title }}</div>\n        <span class=\"version-text\">{{ pageDesc }}</span>\n        <div v-show=\"appVerDisplayState == ShowState.ListAppVer\" class=\"vue-table\" style=\"margin-top: 32px;\">\n            <button class=\"add-new-version\" @click=\"changeDisplay(1)\">ADD NEW</button>\n            <v-client-table ref=\"table\" class=\"versions-table\" :data=\"appVersions\" :columns=\"tableColumns\"\n                :options=\"tableOptions\">\n                <template #createDate=\"props\">\n                    <span>{{ tableMethods.formatDate(props.row.createDate) }}</span>\n                </template>\n                <template #actions=\"props\">\n                    <i v-if=\"props.row.version\" @click=\"deleteVersion(props.row.id)\"\n                        class=\"mdi mdi-delete-outline delete-version\"></i>\n                    <i @click=\"createVersionArtifact(props.row.ID)\" class=\"mdi mdi-puzzle delete-version\"></i>\n                </template>\n            </v-client-table>\n        </div>\n        <div v-show=\"appVerDisplayState == ShowState.CreateaAppVer\">\n            <CreateAppVer :app=\"props.app\" @close=\"changeDisplay(-1)\"></CreateAppVer>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n.version-text {\n    font-size: 16px;\n    color: #616161;\n}\n\n.add-new-version {\n    border-radius: 4px;\n    border: 1px solid #151233;\n    color: #151233;\n    font-weight: 500;\n    font-size: 14px;\n    background-color: transparent;\n    position: absolute;\n    right: 40px;\n    width: 94px;\n    height: 32px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.delete-version {\n    font-size: 16px;\n    color: #757575;\n    cursor: pointer;\n\n}\n\n.title {\n    font-style: normal;\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    white-space: nowrap;\n    color: #151233;\n    margin-top: 40px;\n    margin-bottom: 8px;\n}</style>"],"sourceRoot":""}]);
