ACI,gBAAgB;QAChB,eAAe;QACf,kBAAkB;QAClB,cAAc;QACd,mBAAmB;QACnB,aAAa;QACb,mBAAmB;QACnB,iBAAiB;QACjB,kBAAkB;QAClB,YAAY;AAChB;AACA;QACI,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\n    import { computed, ref } from \"vue\";\n    import { IApplication } from \"../../../../domain/model/models\";\n    import { IIDEInputDefinitions } from \"../../../../domain/presentation/IIDEInputDefinitions\";\n    import IDEInput from \"../../components/application/IDEInput.vue\";\n    import CustomSelect from \"../customSelect.vue\"\n    import AppItemsTable from \"./appItemsTable.vue\"\n    import { ITableData } from \"../../../../../../common/qCloudTemp/exporter\";\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IApplicationVersionUseCase, VersionIncType } from \"../../../../domain/useCase/IApplicationVersionUseCase\";\n    import { IReleaseApplicationRequest } from \"../../../../../../common/qCloudTemp/symDtoObjects\";\n\n    interface IDisabledOptions {\n        index: number\n        cause: string;\n    }\n    interface IAppVersionModel {\n        modelID: string;\n        version: string;\n        modelName: string;\n        path: string;\n        ownerType?: \"module\" | \"application\";\n        ownerName?: string;\n    }\n    interface ICreateAppVersionProp {\n        app?: IApplication\n    }\n    const props = defineProps<ICreateAppVersionProp>();\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const applicationVersionUseCase = container.resolve<IApplicationVersionUseCase>(IApplicationVersionUseCase);\n\n    const releaseTypeOptions = ref([`Major Version (v${getLastRelasedVersion(\"Major\")})`, `Minor Version (v${getLastRelasedVersion(\"Minor\")})`, `Fix Version (v${getLastRelasedVersion(\"Fix\")})`]);\n    const releaseType = ref<VersionIncType>();\n    const disabledOptions = ref<Array<IDisabledOptions>>([]);\n    const isDescriptionValid = ref<boolean>(true);\n    const description = ref(\"\");\n    const appModels = ref<Array<IAppVersionModel>>([]);\n    const emit = defineEmits([\"close\"]);\n    const buttonDisableCond = computed(() => !releaseType.value)\n\n    const validations = ref<Record<\"description\", IIDEInputDefinitions[\"validations\"]>>({\n        description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: false, nonSpaceCharacter: false },\n    });\n    function close() {\n        emit('close');\n    }\n    function setReleaseType(type: VersionIncType) {\n        releaseType.value = (type.split(\" \")[0] as VersionIncType);\n    }\n    function descriptionValidationStatus(isValid: boolean) {\n        isDescriptionValid.value = isValid\n\n    }\n    function setDescValue(desc: string) {\n        description.value = desc\n    }\n    function getItems(item: ITableData) {\n        const model: IAppVersionModel = {\n            modelID: item.id,\n            version: item.version ?? \"latest\",\n            modelName: item.name,\n            path: item.path || \"/\",\n            ownerType: item.ownerType ?? \"application\",\n            ownerName: item.ownerName ?? \"\",\n        }\n        item.checked ? appModels.value?.push(model) : appModels.value = appModels.value?.filter(model => model.modelID != item.id);\n    }\n    function createNewAppVersion() {\n        let request: IReleaseApplicationRequest = {    \n            appID: props.app!.ID,\n            appVersionIncType: releaseType.value!,\n            models: appModels.value,\n            description: description.value,\n        }\n        executor.execute(async () => {\n            await applicationVersionUseCase.releaseApplication(request);\n            close();\n        }, {loading: true})\n    }\n    function getLastRelasedVersion(incType: string) {\n        const [major, minor, fix] = props.app?.lastReleasedVersion ? props.app?.lastReleasedVersion.split(\".\").map(Number) : [0,0,0]\n        switch (incType) {\n            case 'Major':\n                return `${major + 1}.0.0`;\n            case 'Minor':\n                return `${major}.${minor + 1}.0`;\n            case 'Fix':\n                return `${major}.${minor}.${fix + 1}`;\n            default: return;\n        }\n    }\n</script>\n<template>\n    <div class=\"container\">\n        <div class=\"app-version-type\">\n            <label>Type</label>\n            <CustomSelect class=\"select-app-version-type\" :options=\"releaseTypeOptions\" :placeholder=\"'Select Release Type'\"  :selectedClass=\"'selected-type'\" @input=\"setReleaseType\" :disabledOptions=\"disabledOptions\"></CustomSelect>\n        </div>\n        <div style=\"margin: 24px 0 \">\n            <IDEInput :isRequired=\"false\" elementId=\"descriptionInput\" @validateStatus=\"descriptionValidationStatus\" placeholder=\"\" \n                :type=\"'textarea'\" :value=\"description\"   info-text=\"Description\" :validations=\"validations.description\" @getValue=\"setDescValue\" />\n        </div>\n        <AppItemsTable :screenType = \"'Create'\" :buttonCb=\"createNewAppVersion\" @getItem=\"getItems\" :platformSelection=\"{web: true, mobile: true}\" @close=\"close\" :buttonDisableCond=\"buttonDisableCond\"></AppItemsTable>\n    </div>\n</template>\n\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    .container {\n        padding: 20px 0 0 0;\n        height: 600px;\n    }\n    .app-version-type label {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 24px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .select-app-version-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        padding: 0px 12px;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 60px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .create-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .create-button:disabled {\n        color: #757575;\n    }\n</style>\n<style>\n    .selected-type {\n        background-color: transparent;\n        color: #212121;\n        height: 40px;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        /* margin-top: 40px; */\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .select-release-type i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n    .custom-placeholder:empty:before {\n        color: #BCBCBC !important;\n        font-weight: 400;\n    }\n    .custom-select.select-release-type .items {\n        position: static !important;\n    }\n</style>"],"sourceRoot":""}]);
